import React from 'react';
import classNames from 'classnames';
import { isArray, assign } from 'lodash-es';
import { TdDescriptionsItemProps, TdDescriptionsProps } from './type';
import { descriptionsItemDefaultProps, descriptionsDefaultProps } from './defaultProps';
import useDefaultProps from '../hooks/useDefaultProps';
import useConfig from '../hooks/useConfig';
import useCommonClassName from '../hooks/useCommonClassName';
import { StyledProps } from '../common';
import { DescriptionsContext } from './DescriptionsContext';
import DescriptionsItem from './DescriptionsItem';
import Row from './Row';

import type { DescriptionsItemProps } from './DescriptionsItem';

/**
 * 
 * 1.  table tbody tr td 
 * 2.  span  item 
 * 3. column  span /column  span  item
 * 4. item /
 */

/**
 * TDescriptions headertitle  bodytable, tbody
 * TDescriptionsRowtr
 * TDescriptionsItem item span, label, content
 */

export type DescriptionsProps = TdDescriptionsProps &
  StyledProps & {
    children?: React.ReactNode;
  };

const Descriptions = (DescriptionsProps: DescriptionsProps) => {
  const props = useDefaultProps<DescriptionsProps>(DescriptionsProps, descriptionsDefaultProps);

  const { className, style, title, bordered, column, layout, items: rowItems, children, tableLayout } = props;

  const { classPrefix } = useConfig();

  const COMPONENT_NAME = `${classPrefix}-descriptions`;

  const { SIZE } = useCommonClassName();

  // 
  const getRows = () => {
    // 1. a. props  items b. slots t-descriptions-item; a 

    let items: TdDescriptionsItemProps[] = [];

    if (isArray(rowItems)) {
      /**
       * 2.1 a  items
       * !  label: string / <div></div> / () =>  <div></div>
       * ! 
       */
      items = rowItems.map((item) => {
        const { span } = assign({}, descriptionsItemDefaultProps, item);
        return {
          label: item.label,
          content: item.content,
          span,
        };
      });
    } else {
      // 2.2 b   TDescriptionsItem
      const childrenList = React.Children.toArray(children).filter(
        (child: React.ReactElement<DescriptionsItemProps>) =>
          (child.type as any)?.displayName === DescriptionsItem.displayName,
      );

      if (childrenList.length !== 0) {
        items = (childrenList as React.ReactElement[]).map(
          ({
            props: child,
          }: {
            props: TdDescriptionsItemProps & {
              children?: React.ReactNode;
            };
          }) => {
            const { span } = assign({}, descriptionsItemDefaultProps, child);

            return {
              label: child.label,
              content: child.content ?? child.children,
              span,
            };
          },
        );
      }
    }

    // 2.  'vertical'
    if (layout === 'vertical') {
      return [items];
    }
    // 3.  horizontal  item 
    let temp: TdDescriptionsItemProps[] = [];
    let reset = column;
    // 4. 
    const res: TdDescriptionsItemProps[][] = [];
    items.forEach((item, index) => {
      const { span } = item;
      if (reset >= span) {
        // 
        temp.push(item);
        reset -= span;
      } else {
        // 
        res.push(temp);
        temp = [item];
        reset = column - span;
      }

      if (index === items.length - 1) {
        // 
        Reflect.set(item, 'span', span + reset);
        res.push(temp);
      }
    });

    return res;
  };

  // Header
  const renderHeader = () => (title ? <div className={`${COMPONENT_NAME}__header`}>{title}</div> : '');

  // Body
  const renderBody = () => {
    const tableClass = [
      `${COMPONENT_NAME}__body`,
      SIZE[props.size],
      { [`${COMPONENT_NAME}__body--fixed`]: tableLayout === 'fixed' },
      { [`${COMPONENT_NAME}__body--border`]: bordered },
    ];
    return (
      <table className={classNames(tableClass)}>
        <tbody>
          {getRows().map((row, i) => (
            <Row row={row} key={i} />
          ))}
        </tbody>
      </table>
    );
  };

  return (
    <DescriptionsContext.Provider value={props}>
      <div className={classNames(className, COMPONENT_NAME)} style={style}>
        {renderHeader()}
        {renderBody()}
      </div>
    </DescriptionsContext.Provider>
  );
};

Descriptions.displayName = 'Descriptions';

Descriptions.DescriptionsItem = DescriptionsItem;

export default Descriptions;
